.p {
  width: 30%;
  height: 40vh;
  border: 2px solid rgb(243, 242, 242);
  margin: 20px 10px;
  border-radius: 10px 10px 0px 0px;
  overflow: hidden;
}

.p-browser {
  height: 30px;
  background-color: rgb(243, 242, 242);
  display: flex;
  justify-content: center;
  align-items: center;
  position: sticky;
  z-index: 2;
  text-align: center;
  color: #333;
}

.heading-section {
  z-index: 2;
}

.p-circle {
  height: 6px;
  width: 6px;
  border-radius: 50%;
  background-color: white;
  margin: 3px;
}

.p-img {
  width: 100%;
  transition: all 10s ease;
}

.p-img:hover {
  transform: translateY(-100%);
}

@media screen and (min-width: 482px) and (max-width: 810px) {
  .p {
    width: 40%;
    height: 30vh;
  }
}

@media screen and (max-width: 480px) {
  .p {
    width: 100%;
  }
}
